<template>
	<div ref="wrapper">
		<slot></slot>
	</div>
</template>

<script>	
  import BScroll from 'better-scroll'

	export default {
		props: {
			probeType: {
        type:Number,
        default: 1
      },
			data: {
				type: Array,
				default: null
			},
      click: {
      	type:Boolean,
      	default: true
      },
      listenScroll: {
      	type: Boolean,
      	default: false
      },
			refreshDelay: {
				type: Number,
				default: 20
			}
		},
		mounted() {
			setTimeout(() => {
				this._initScroll()
			}, 20)
		},
		methods: {
			_initScroll() {
				if (!this.$refs.wrapper) {
					return
				}

				this.scroll = new BScroll(
					this.$refs.wrapper, {
						probeType: this.probeType
					}
				)

				if (this.listenScroll) {
					let me = this
					this.scroll.on('scroll', (pos) => {
						me.$emit('scroll', pos)
					})
				}
			},
    	disable() {
    	  this.scroll && this.scroll.disable()
    	},
    	enable() {
    	  this.scroll && this.scroll.enable()
    	},
			refresh() {
				this.scroll && this.scroll.refresh()
			},
			scrollTo() {
				this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
			},
			scrollToElement() {
				this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
			}
		},
		watch: {
			data() {
				setTimeout(() => {
					this.refresh()
				}, this.refreshDelay)
			}
		}
	}
</script>

<style lang="sass" scoped>
	
</style>


